        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  . &nbsp;    &nbsp; .
               &nbsp; &nbsp;     .
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ info.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{ info.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ info.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ info.email }}
              </span>
            </li>
            <li class="dictionary__item" v-if="info.comment">
              <span class="dictionary__key">
                  
              </span>
              <span class="dictionary__value">
                {{ info.comment }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                  
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in products" :key="item.productId">
              <h3>{{ item.product.title }}</h3>
              <b>{{ item.product.price | numberFormat }}  x {{ item.amount }}</b>
              <span>: {{ item.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>: <b>500 </b></p>
            <p>: <b>{{ totalProducts }}</b> {{ totalProductsStr }}   <b>{{ info.totalPrice | numberFormat }} </b></p>
          </div>
